
.time-unit{z-index:4;position:relative;transition:transform .2s ease;-webkit-user-select:none;-webkit-tap-highlight-color:transparent
}
.time-unit:hover{transform:scale(1.1)
}
.time-unit__value{position:relative;display:inline-block;z-index:2;color:#fff;font-size:50px;line-height:50px;padding:0 2px;text-align:center;width:34px;height:50px;background:transparent;border:none;outline:none;cursor:pointer;transition:all .3s ease
}
.time-unit__value:focus,.time-unit__value:active{background:transparent
}
.time-unit__value-enter{opacity:0;transform:translate3d(0, 2rem, 0) scale(0.7)
}
.time-unit__value-leave-active{position:absolute;opacity:0;transform:translate3d(0, -2rem, 0) scale(0.7)
}
.time-unit__value--reverse-enter{opacity:0;transform:translate3d(0, -2rem, 0) scale(0.7)
}
.time-unit__value--reverse-leave-active{position:absolute;opacity:0;transform:translate3d(0, 2rem, 0) scale(0.7)
}



