
.timepicker-wrap{display:inline-block;position:relative
}
.timepicker-wrap *,.timepicker-wrap *::after,.timepicker-wrap *::before{box-sizing:border-box
}
.time{border:1px solid rgba(0,0,0,0.1);padding:15px 10px 15px 40px;border-radius:3px;font-size:1.2rem;width:120px;text-align:center
}
.flex-wrap{display:flex;position:relative
}
.timepicker-icon{position:absolute;left:15px;top:50%;display:inline-block;width:1em;height:1em;stroke-width:0;stroke:currentColor;fill:currentColor;transform:translate(0, -50%)
}
.timepicker{position:absolute;background:#FBFBFF;width:100vw;box-shadow:0 3px 10px rgba(0,0,0,0.3);border-radius:3px;overflow:hidden;left:50%;top:-135px;border-radius:50%;transition:all .3s ease;transform:translate3d(-50%, 0, 0) scale(0);pointer-events:none
}
@media (min-width: 480px){
.timepicker{width:250px;border-radius:0
}
}
.timepicker.is-open{transform:translate3d(-50%, 0, 0) scale(1);border-radius:3px;pointer-events:auto
}
.timepicker__header{padding:5px 15px;font-size:14px;color:#fff;background:#F25F5C
}
.timepicker__time{position:relative;overflow:hidden;background:#f68d8b;display:flex;justify-content:center;font-size:50px;color:#fff;line-height:1;padding:12px 10px
}
.timepicker__active-bg{position:absolute;top:-25%;left:0;width:34px;height:200%;background:#F25F5C;transition:transform .4s ease
}
.timepicker__separator{z-index:2;position:relative;width:34px;text-align:center;font-size:90%
}



