
.triangle{display:inline-block;width:1em;height:1em;stroke-width:0;stroke:currentColor;fill:currentColor
}
.numpad__digits{display:flex;flex-wrap:wrap;padding:5px 20px
}
.numpad__digits .numpad__digit:last-of-type{margin-left:auto;margin-right:auto
}
.numpad__digit{position:relative;width:33%
}
.numpad__digit button{position:relative;z-index:2;display:block;width:100%;padding:20px 0;text-align:center;color:#757575;background:none;border:none;font-size:16px;font-weight:600;line-height:1.3;cursor:pointer;transition:color .3s ease;outline:none;-webkit-user-select:none;-webkit-tap-highlight-color:transparent
}
@media (min-width: 480px){
.numpad__digit button{padding:15px 0
}
}
.numpad__digit button.is-disabled{color:rgba(117,117,117,0.3)
}
.numpad__digit button:focus+.numpad__ripple{opacity:.3;transform:translate(-50%, -50%) scale(1)
}
.numpad__arrows{display:flex;flex-wrap:wrap;position:absolute;padding:5px 20px;bottom:0;left:0;right:0;justify-content:space-between
}
.numpad__arrows button{font-size:25px;padding:17px 0 13px 0;line-height:1
}
@media (min-width: 480px){
.numpad__arrows button{padding:13px 0 8px 0
}
}
.numpad__ripple{z-index:1;position:absolute;left:50%;top:50%;background:rgba(0,0,0,0.2);border-radius:50%;width:55px;height:55px;opacity:0;transform:translate(-50%, -50%) scale(1.1);transition:.3s
}
@media (min-width: 480px){
.numpad__ripple{width:45px;height:45px
}
}
.numpad__ripple.is-pressed{opacity:.3;transform:translate(-50%, -50%) scale(1)
}



